import React,{useState,Fragment} from 'react';
import './Stylling.css';
import {BrowserRouter as Router,Link,Route,Switch} from 'react-router-dom';
import data from './mock-data.json';
import {nanoid} from 'nanoid';
import Userinfor from './Userinfor';


const Landing = () => {
    const [contacts,setContacts]=useState(data);
    const [addFormData,setAddFormData]=useState(
        {
            fullName:'',
            address:'',
            phonenumber:'', 
            email:'' 
        }

    )

    const handleAddFormChange=(e)=>{
        e.preventDefault();
        const fieldName =e.target.getAttribute("name");
        const fieldValue =e.target.value;

        const newformData={...addFormData}; 
        newformData[fieldName]=fieldValue;
        setAddFormData(newformData); 
    }

    
    const handleAddFormSubmit=(e)=>{
        e.preventDefault();
        const newContact={
            id:nanoid(),
            fullName:addFormData.fullName, 
            address:addFormData.address,
            phonenumber:addFormData.phonenumber,
            email:addFormData.email,
        };
        const newContacts=[...contacts,newContact];
    setContacts(newContacts);
    };

    

   const handleDeleteClick=(contactId)=>{
       const newContacts =[...contacts];

       const index=contacts.findIndex((contact)=>contact.id === contactId)
       newContacts.splice(index,1);

       setContacts(newContacts);
   } 
   


    return ( 
        <Router>
        <div className="coverpage">
        <form onSubmit={handleAddFormSubmit} className="form1">
            <h2>REGISTRATION FORM</h2>
            <table>
                <tr>
                    <th><i class="fa fa-user" aria-hidden="true"></i> Name</th>
                    <td>
                    <input type="text"
             name="fullName"
              required="required"
              placeholder="Enter your firstname.."
              onChange={handleAddFormChange}></input>
                    </td>
                </tr>
                <tr>
                    <th><i class="fa fa-user-circle-o" aria-hidden="true"></i>
                    Surname</th>
                    <td>
                    <input type="text"
             name="address"
              required="required"
              placeholder="Enter your Surname.."
              onChange={handleAddFormChange}/>
                    </td>
                </tr>
                <tr>
                    <th><i class="fa fa-map-marker" aria-hidden="true"></i>
                    Location</th>
                    <td>
                    <input type="text"
             name="phonenumber"
              required="required"
              placeholder="Enter your location.."
              onChange={handleAddFormChange}/>
                    </td>
                </tr>
                <tr>
                    <th><i class="fa fa-envelope" aria-hidden="true"></i> Email</th>
                    <td>
                    <input type="text"
             name="email"
              required="required"
              placeholder="Enter your email.."
              onChange={handleAddFormChange}/>
              
                    </td>
                </tr>
                
                <tr><button type="submit">REGISTER</button></tr>
            </table>
              
        </form>
        <div className="boxuser">
       <h2>USERS</h2>
            

{contacts.length>0 ?(contacts.map((action) =>
            <Link to={"/UsersInfor/"+ action.id}>
            
            <p>{action.fullName}  {action.address}    
              </p></Link>)):(
                  <div></div>
              )
            }
            </div>
            
            <Switch>
            <Route path="/UsersInfor/:id">
           {contacts.map((d)=>(
               <Fragment>
                   
                   <Userinfor  d={d}
                   handleDeleteClick={handleDeleteClick}
                   />
                         
                        </Fragment>
                        ))}
                    </Route>
            </Switch>
            
        </div>
        </Router>
     );
}
 
export default Landing;